import { useParams, useLocation } from 'react-router-dom';
// material
import { Container } from '@material-ui/core';
// routes
import { PATH_DASHBOARD } from '../routes/paths';
// hooks
import useSettings from '../hooks/useSettings';
// components
import Page from '../components/Page';
import HeaderBreadcrumbs from '../components/HeaderBreadcrumbs';
import BrandNewForm from '../components/brand/BrandNewForm';

// ----------------------------------------------------------------------

export default function BrandCreate() {
  const { themeStretch } = useSettings();
  const { pathname } = useLocation();
  const { name } = useParams();
  const isEdit = pathname.includes('edit');

  // TODO: will be updated with brand
  const currentBrand = {};

  return (
    <Page title="Brand: Create a new brand">
      <Container maxWidth={themeStretch ? false : 'lg'}>
        <HeaderBreadcrumbs
          heading={!isEdit ? 'Add Brand' : 'Edit Brand'}
          links={[{ name: 'Brand', href: PATH_DASHBOARD.root }, { name: !isEdit ? 'Add Brand' : name }]}
        />
        <BrandNewForm isEdit={isEdit} currentBrand={currentBrand} />
      </Container>
    </Page>
  );
}
