import * as Yup from 'yup';
import PropTypes from 'prop-types';
import React, { useCallback } from 'react';
import { useSnackbar } from 'notistack5';
import { useNavigate } from 'react-router-dom';
import { FieldArray, Form, FormikProvider, getIn, useFormik } from 'formik';
// material
import { Add as AddIcon, Delete as DeleteIcon, Save as SaveIcon } from '@material-ui/icons';
import { LoadingButton } from '@material-ui/lab';
import { styled } from '@material-ui/core/styles';
import {
  Box,
  Card,
  Grid,
  Stack,
  Switch,
  TextField,
  Typography,
  FormHelperText,
  FormControlLabel,
  IconButton,
  CardHeader,
  CardContent,
} from '@material-ui/core';
// utils
import { fData } from '../../utils/formatNumber';
// routes
import { PATH_DASHBOARD } from '../../routes/paths';
// utils
import countries from '../../utils/constants/countries';
import { phoneRegExp, addressRegExp, nameRegExp } from '../../utils/constants/regexs';
// components
import { UploadAvatar, UploadSingleFile } from '../upload';
import DynamicFormItem from '../DynamicFormItem';

// ----------------------------------------------------------------------

const LabelStyle = styled(Typography)(({ theme }) => ({
  ...theme.typography.subtitle2,
  color: theme.palette.text.secondary,
  marginBottom: theme.spacing(1),
}));

const NewUserSchema = Yup.object().shape({
  avatarUrl: Yup.mixed().required('Avatar is required'),
  name: Yup.string().matches(nameRegExp, 'Cannot include special characters.').required('Name is required'),
  contactPersonName: Yup.string().matches(nameRegExp, 'Cannot include special characters.'),
  email: Yup.string().email().required('Email is required'),
  phoneNumber: Yup.string().matches(phoneRegExp, 'Phone number is not valid.').required('Phone number is required'),
  address: Yup.string().matches(addressRegExp, 'Address cannot include special characters.'),
  country: Yup.string(),
  state: Yup.string(),
  city: Yup.string(),
  zipCode: Yup.string(),
  status: Yup.boolean().required('Status is required'),
  about: Yup.string(),
  websiteLinks: Yup.array().of(Yup.string()),
  facebookLinks: Yup.array().of(Yup.string()),
  instagramLinks: Yup.array().of(Yup.string()),
  twitterLinks: Yup.array().of(Yup.string()),
  whoWeAreLookingFor: Yup.string(),
  brandRestriction: Yup.string(),
  coverUrl: Yup.mixed(),
});

BrandNewForm.propTypes = {
  isEdit: PropTypes.bool,
  currentBrand: PropTypes.object,
};

export default function BrandNewForm({ isEdit, currentBrand }) {
  const navigate = useNavigate();
  const { enqueueSnackbar } = useSnackbar();

  const formik = useFormik({
    enableReinitialize: true,
    initialValues: {
      avatarUrl: currentBrand?.avatarUrl || null,
      name: currentBrand?.name || '',
      contactPersonName: currentBrand?.contactPersonName || '',
      email: currentBrand?.email || '',
      phoneNumber: currentBrand?.phoneNumber || '',
      address: currentBrand?.address || '',
      country: currentBrand?.country || '',
      state: currentBrand?.state || '',
      city: currentBrand?.city || '',
      zipCode: currentBrand?.zipCode || undefined,
      status: currentBrand?.status || true,
      websiteLinks: currentBrand?.websiteLinks || [''],
      facebookLinks: currentBrand?.facebookLinks || [''],
      instagramLinks: currentBrand?.instagramLinks || [''],
      twitterLinks: currentBrand?.twitterLinks || [''],
      whoWeAreLookingFor: currentBrand?.whoWeAreLookingFor || '',
      brandRestriction: currentBrand?.brandRestriction || '',
      coverUrl: currentBrand?.coverUrl || null,
    },
    validationSchema: NewUserSchema,
    onSubmit: async (values, { setSubmitting, resetForm, setErrors }) => {
      alert(JSON.stringify(values));
      try {
        resetForm();
        setSubmitting(false);
        enqueueSnackbar(!isEdit ? 'Create success' : 'Update success', { variant: 'success' });
        navigate(PATH_DASHBOARD.brand.list);
      } catch (error) {
        console.error(error);
        setSubmitting(false);
        setErrors(error);
      }
    },
  });

  const { errors, values, touched, handleSubmit, isSubmitting, setFieldValue, getFieldProps } = formik;

  console.log('*** ', values, errors);

  const handleAvatarDrop = useCallback(
    (acceptedFiles) => {
      const file = acceptedFiles[0];
      if (file) {
        setFieldValue('avatarUrl', {
          ...file,
          preview: URL.createObjectURL(file),
        });
      }
    },
    [setFieldValue]
  );

  const handleCoverDrop = useCallback(
    (acceptedFiles) => {
      const file = acceptedFiles[0];
      if (file) {
        setFieldValue('coverUrl', {
          ...file,
          preview: URL.createObjectURL(file),
        });
      }
    },
    [setFieldValue]
  );

  return (
    <FormikProvider value={formik}>
      <Form noValidate autoComplete="off" onSubmit={handleSubmit}>
        <Grid container spacing={3}>
          <Grid item xs={12} md={4}>
            <Card sx={{ py: 10, px: 3 }}>
              <Box sx={{ mb: 5 }}>
                <UploadAvatar
                  accept="image/*"
                  file={values.avatarUrl}
                  maxSize={3145728}
                  onDrop={handleAvatarDrop}
                  error={Boolean(touched.avatarUrl && errors.avatarUrl)}
                  caption={
                    <Typography
                      variant="caption"
                      sx={{
                        mt: 2,
                        mx: 'auto',
                        display: 'block',
                        textAlign: 'center',
                        color: 'text.secondary',
                      }}
                    >
                      Allowed *.jpeg, *.jpg, *.png, *.gif
                      <br /> max size of {fData(3145728)}
                    </Typography>
                  }
                />
                <FormHelperText error sx={{ px: 2, textAlign: 'center' }}>
                  {touched.avatarUrl && errors.avatarUrl}
                </FormHelperText>
              </Box>

              <FormControlLabel
                labelPlacement="start"
                control={<Switch {...getFieldProps('status')} checked={values.status} />}
                label={
                  <Typography variant="subtitle2" sx={{ mb: 0.5 }}>
                    Status
                  </Typography>
                }
                sx={{ mx: 0, width: 1, justifyContent: 'space-between' }}
              />
            </Card>
          </Grid>

          <Grid item xs={12} md={8}>
            <Card sx={{ p: 3 }}>
              <Stack spacing={3}>
                <Stack direction={{ xs: 'column', sm: 'row' }} spacing={{ xs: 3, sm: 2 }}>
                  <TextField
                    fullWidth
                    size="small"
                    label="Name"
                    {...getFieldProps('name')}
                    error={Boolean(touched.name && errors.name)}
                    helperText={touched.name && errors.name}
                  />
                  <TextField
                    fullWidth
                    size="small"
                    label="Contact Person Name"
                    {...getFieldProps('contactPersonName')}
                    error={Boolean(touched.contactPersonName && errors.contactPersonName)}
                    helperText={touched.contactPersonName && errors.contactPersonName}
                  />
                </Stack>

                <Stack direction={{ xs: 'column', sm: 'row' }} spacing={{ xs: 3, sm: 2 }}>
                  <TextField
                    fullWidth
                    size="small"
                    label="Phone Number"
                    {...getFieldProps('phoneNumber')}
                    error={Boolean(touched.phoneNumber && errors.phoneNumber)}
                    helperText={touched.phoneNumber && errors.phoneNumber}
                  />
                  <TextField
                    fullWidth
                    size="small"
                    label="Email Address"
                    {...getFieldProps('email')}
                    error={Boolean(touched.email && errors.email)}
                    helperText={touched.email && errors.email}
                  />
                </Stack>

                <Stack direction={{ xs: 'column', sm: 'row' }} spacing={{ xs: 3, sm: 2 }}>
                  <TextField
                    fullWidth
                    multiline
                    rows={3}
                    size="small"
                    label="Address"
                    {...getFieldProps('address')}
                    error={Boolean(touched.address && errors.address)}
                    helperText={touched.address && errors.address}
                  />
                </Stack>

                <Stack direction={{ xs: 'column', sm: 'row' }} spacing={{ xs: 3, sm: 2 }}>
                  <TextField
                    select
                    fullWidth
                    size="small"
                    label="Country"
                    placeholder="Country"
                    {...getFieldProps('country')}
                    SelectProps={{ native: true }}
                    error={Boolean(touched.country && errors.country)}
                    helperText={touched.country && errors.country}
                  >
                    <option value="" />
                    {countries.map((option) => (
                      <option key={option.code} value={option.label}>
                        {option.label}
                      </option>
                    ))}
                  </TextField>
                  <TextField
                    fullWidth
                    size="small"
                    label="State/Region"
                    {...getFieldProps('state')}
                    error={Boolean(touched.state && errors.state)}
                    helperText={touched.state && errors.state}
                  />
                </Stack>

                <Stack direction={{ xs: 'column', sm: 'row' }} spacing={{ xs: 3, sm: 2 }}>
                  <TextField
                    fullWidth
                    size="small"
                    label="City"
                    {...getFieldProps('city')}
                    error={Boolean(touched.city && errors.city)}
                    helperText={touched.city && errors.city}
                  />
                  <TextField fullWidth size="small" label="Zip/Code" {...getFieldProps('zipCode')} />
                </Stack>

                <Card>
                  <CardHeader title="Social Media Links" />
                  <CardContent>
                    <Stack spacing={1}>
                      <FieldArray
                        name="websiteLinks"
                        render={(arrHelper) => {
                          const value = values.websiteLinks;
                          return (
                            <>
                              {value.map((item, index) => {
                                const fieldName = `websiteLinks.${index}`;
                                return (
                                  <DynamicFormItem
                                    key={index}
                                    label={`Website Link ${index + 1}`}
                                    fieldProps={{ ...getFieldProps(fieldName) }}
                                    error={Boolean(getIn(touched, fieldName) && getIn(errors, fieldName))}
                                    helperText={getIn(touched, fieldName) && getIn(errors, fieldName)}
                                    isLastItem={index === value.length - 1}
                                    onAddClick={() => arrHelper.push('')}
                                    onRemoveClick={() => arrHelper.remove(index)}
                                  />
                                );
                              })}
                            </>
                          );
                        }}
                      />

                      <FieldArray
                        name="facebookLinks"
                        render={(arrHelper) => {
                          const value = values.facebookLinks;
                          return (
                            <>
                              {value.map((item, index) => {
                                const fieldName = `facebookLinks.${index}`;
                                return (
                                  <DynamicFormItem
                                    key={index}
                                    label={`Facebook Link ${index + 1}`}
                                    fieldProps={{ ...getFieldProps(fieldName) }}
                                    error={Boolean(getIn(touched, fieldName) && getIn(errors, fieldName))}
                                    helperText={getIn(touched, fieldName) && getIn(errors, fieldName)}
                                    isLastItem={index === value.length - 1}
                                    onAddClick={() => arrHelper.push('')}
                                    onRemoveClick={() => arrHelper.remove(index)}
                                  />
                                );
                              })}
                            </>
                          );
                        }}
                      />
                      <FieldArray
                        name="instagramLinks"
                        render={(arrHelper) => {
                          const value = values.instagramLinks;
                          return (
                            <>
                              {value.map((item, index) => {
                                const fieldName = `instagramLinks.${index}`;
                                return (
                                  <DynamicFormItem
                                    key={index}
                                    label={`Instagram Link ${index + 1}`}
                                    fieldProps={{ ...getFieldProps(fieldName) }}
                                    error={Boolean(getIn(touched, fieldName) && getIn(errors, fieldName))}
                                    helperText={getIn(touched, fieldName) && getIn(errors, fieldName)}
                                    isLastItem={index === value.length - 1}
                                    onAddClick={() => arrHelper.push('')}
                                    onRemoveClick={() => arrHelper.remove(index)}
                                  />
                                );
                              })}
                            </>
                          );
                        }}
                      />
                      <FieldArray
                        name="twitterLinks"
                        render={(arrHelper) => {
                          const value = values.twitterLinks;
                          return (
                            <>
                              {value.map((item, index) => {
                                const fieldName = `twitterLinks.${index}`;
                                return (
                                  <DynamicFormItem
                                    key={index}
                                    label={`Twitter Link ${index + 1}`}
                                    fieldProps={{ ...getFieldProps(fieldName) }}
                                    error={Boolean(getIn(touched, fieldName) && getIn(errors, fieldName))}
                                    helperText={getIn(touched, fieldName) && getIn(errors, fieldName)}
                                    isLastItem={index === value.length - 1}
                                    onAddClick={() => arrHelper.push('')}
                                    onRemoveClick={() => arrHelper.remove(index)}
                                  />
                                );
                              })}
                            </>
                          );
                        }}
                      />
                    </Stack>
                  </CardContent>
                </Card>

                <Stack direction={{ xs: 'column', sm: 'row' }} spacing={{ xs: 3, sm: 2 }}>
                  <TextField
                    fullWidth
                    multiline
                    rows={4}
                    size="small"
                    label="Who we are looking for"
                    {...getFieldProps('whoWeAreLookingFor')}
                    error={Boolean(touched.whoWeAreLookingFor && errors.whoWeAreLookingFor)}
                    helperText={touched.whoWeAreLookingFor && errors.whoWeAreLookingFor}
                  />
                </Stack>

                <Stack direction={{ xs: 'column', sm: 'row' }} spacing={{ xs: 3, sm: 2 }}>
                  <TextField
                    fullWidth
                    multiline
                    rows={4}
                    size="small"
                    label="Brand Restriction"
                    {...getFieldProps('brandRestriction')}
                    error={Boolean(touched.brandRestriction && errors.brandRestriction)}
                    helperText={touched.brandRestriction && errors.brandRestriction}
                  />
                </Stack>

                <Stack>
                  <LabelStyle>Cover</LabelStyle>
                  <UploadSingleFile
                    maxSize={3145728}
                    accept="image/*"
                    file={values.coverUrl}
                    onDrop={handleCoverDrop}
                    error={Boolean(touched.coverUrl && errors.coverUrl)}
                  />
                  {touched.coverUrl && errors.coverUrl && (
                    <FormHelperText error sx={{ px: 2 }}>
                      {touched.coverUrl && errors.coverUrl}
                    </FormHelperText>
                  )}
                </Stack>

                <Box sx={{ mt: 3, display: 'flex', justifyContent: 'flex-end' }}>
                  <LoadingButton type="submit" variant="contained" loading={isSubmitting} endIcon={<SaveIcon />}>
                    Save
                  </LoadingButton>
                </Box>
              </Stack>
            </Card>
          </Grid>
        </Grid>
      </Form>
    </FormikProvider>
  );
}
